"use client";

import { useState, useEffect, useMemo, Fragment } from "react";
import axios from "axios";
import Fuse from "fuse.js";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Dialog, Transition } from "@headlessui/react";
import { motion } from "framer-motion";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import { Button } from "@/components/ui/button";
import { Download, Sun, Moon } from "lucide-react";

// Define the Course interface
interface Course {
  courseReferenceNumber: string;
  courseTitle: string;
  courseMajor: string;
  subjectCourse: string;
  faculty?: { displayName: string }[];
  meetingsFaculty?: {
    meetingTime: {
      beginTime: string;
      endTime: string;
      monday: boolean;
      tuesday: boolean;
      wednesday: boolean;
      thursday: boolean;
      friday: boolean;
      buildingDescription?: string;
      room?: string;
    };
  }[];
}

export default function Home() {
  const [courses, setCourses] = useState<Course[]>([]);
  const [selectedCourses, setSelectedCourses] = useState<Course[]>([]);
  const [search, setSearch] = useState("");
  const [theme, setTheme] = useState("light");

  // Fetch courses from the backend
  useEffect(() => {
    axios
      .get("http://127.0.0.1:5001/api/courses")
      .then((res) => setCourses(res.data))
      .catch((err) => console.error(err));
  }, []);

  // Toggle dark/light theme
  useEffect(() => {
    document.documentElement.classList.toggle("dark", theme === "dark");
  }, [theme]);

  // Function to add or remove courses from the selected list
  const toggleCourse = (course: Course) => {
    setSelectedCourses((prev) =>
      prev.find((c) => c.courseReferenceNumber === course.courseReferenceNumber)
        ? prev.filter((c) => c.courseReferenceNumber !== course.courseReferenceNumber)
        : [...prev, course]
    );
  };

  // Helper function to get a random color
  function getRandomColor() {
    const colors = ["#FF5733", "#33FF57", "#3357FF", "#F333FF", "#FF33F6", "#33FFF3", "#F3FF33"];
    return colors[Math.floor(Math.random() * colors.length)];
  }

  // ------------------ Color Mapping ------------------
  // Using useMemo to create a mapping of courseReferenceNumber to a random color.
  const courseColors = useMemo(() => {
    const mapping: Record<string, string> = {};
    courses.forEach((course) => {
      mapping[course.courseReferenceNumber] = getRandomColor();
    });
    return mapping;
  }, [courses]);
  // ---------------------------------------------------

  // Create a Fuse.js instance for fuzzy search on multiple keys
  const fuse = new Fuse(courses, {
    keys: [
      "courseTitle",
      "courseMajor",
      "subjectCourse",
      {
        name: "faculty",
        getFn: (course: Course) =>
          course.faculty?.map((f) => {
            const parts = f.displayName.split(", ");
            return parts.length === 2 ? `${parts[1]} ${parts[0]}` : f.displayName;
          }) || [],
      },
    ],
    threshold: 0.4,
  });

  // Use Fuse.js for fuzzy filtering of courses
  const filteredCourses = search.trim()
    ? fuse.search(search).map((result) => result.item)
    : courses;

  // Function to export schedule to ICS
  const exportToICS = () => {
    const events = selectedCourses.flatMap((course) =>
      course.meetingsFaculty?.map((meeting) => {
        if (!meeting.meetingTime) return null;

        const { beginTime, endTime, monday, tuesday, wednesday, thursday, friday } = meeting.meetingTime;
        const days = [monday, tuesday, wednesday, thursday, friday];
        const location = meeting.meetingTime.buildingDescription && meeting.meetingTime.room
          ? `${meeting.meetingTime.buildingDescription} - ${meeting.meetingTime.room}`
          : "TBA";

        return days
          .map((day, index) => {
            if (!day) return null;

            // Create a date object for the current week
            const now = new Date();
            const currentDay = now.getDay();
            const daysUntilTarget = (index + 1) - currentDay;
            const targetDate = new Date(now);
            targetDate.setDate(now.getDate() + daysUntilTarget);

            // Format times
            const formattedStartTime = beginTime ? `${beginTime.substring(0, 2)}:${beginTime.substring(2)}` : "00:00";
            const formattedEndTime = endTime ? `${endTime.substring(0, 2)}:${endTime.substring(2)}` : "00:00";

            // Create ICS event
            const event = {
              title: course.courseTitle,
              start: `${targetDate.toISOString().split('T')[0]}T${formattedStartTime}:00`,
              end: `${targetDate.toISOString().split('T')[0]}T${formattedEndTime}:00`,
              location,
              description: `CRN: ${course.courseReferenceNumber}\nFaculty: ${course.faculty?.map(f => f.displayName).join(", ") || "TBA"}`,
            };

            return `BEGIN:VEVENT
SUMMARY:${event.title}
DTSTART:${event.start.replace(/[-:]/g, "")}Z
DTEND:${event.end.replace(/[-:]/g, "")}Z
LOCATION:${event.location}
DESCRIPTION:${event.description}
END:VEVENT`;
          })
          .filter(Boolean);
      })
    ).filter(Boolean);

    const icsContent = `BEGIN:VCALENDAR
VERSION:2.0
PRODID:-//Blink Schedule Maker//EN
${events.join("\n")}
END:VCALENDAR`;

    const blob = new Blob([icsContent], { type: "text/calendar" });
    const url = window.URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", "schedule.ics");
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
  };

  // Convert selected courses into FullCalendar events
  const calendarEvents = useMemo(() => {
    return selectedCourses.flatMap((course) =>
      course.meetingsFaculty?.map((meeting) => {
        if (!meeting.meetingTime) return null;

        const { beginTime, endTime, monday, tuesday, wednesday, thursday, friday } = meeting.meetingTime;
        const days = [monday, tuesday, wednesday, thursday, friday];
        const randomColor = courseColors[course.courseReferenceNumber];

        return days
          .map((day, index) =>
            day
              ? {
                  id: `${course.courseReferenceNumber}-${index}`,
                  title: course.courseTitle,
                  startTime: beginTime ? `${beginTime.substring(0, 2)}:${beginTime.substring(2)}` : "00:00",
                  endTime: endTime ? `${endTime.substring(0, 2)}:${endTime.substring(2)}` : "00:00",
                  daysOfWeek: [index + 1],
                  backgroundColor: randomColor,
                  borderColor: randomColor,
                  textColor: "#fff",
                  extendedProps: {
                    courseRef: course.courseReferenceNumber,
                    location: meeting.meetingTime.buildingDescription && meeting.meetingTime.room
                      ? `${meeting.meetingTime.buildingDescription} - ${meeting.meetingTime.room}`
                      : "TBA",
                    faculty: course.faculty?.map(f => f.displayName).join(", ") || "TBA"
                  }
                }
              : null
          )
          .filter(Boolean);
      })
    ).filter(Boolean);
  }, [selectedCourses, courseColors]);

  return (
    <div className="min-h-screen bg-background transition-all">
      <div className="container mx-auto p-6">
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-primary to-primary/60">
              Blink
            </h1>
            <p className="text-muted-foreground mt-1">Create your perfect schedule</p>
          </div>
          <div className="flex items-center gap-4">
            <Button
              variant="outline"
              size="icon"
              onClick={() => setTheme(theme === "light" ? "dark" : "light")}
              className="rounded-full"
            >
              {theme === "light" ? <Moon className="h-4 w-4" /> : <Sun className="h-4 w-4" />}
            </Button>
            <Button
              onClick={exportToICS}
              disabled={selectedCourses.length === 0}
              className="gap-2"
            >
              <Download className="h-4 w-4" />
              Export Schedule
            </Button>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Left Sidebar (1/3 width) for course search & selection */}
          <div className="lg:col-span-1 bg-card rounded-xl shadow-sm border p-4 h-[calc(100vh-12rem)] overflow-y-auto">
            <div className="relative mb-4">
              <input
                type="text"
                placeholder="Search courses..."
                className="w-full p-3 pl-10 rounded-lg border bg-background focus:outline-none focus:ring-2 focus:ring-primary/20 transition-all"
                onChange={(e) => setSearch(e.target.value)}
              />
              <svg
                className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                />
              </svg>
            </div>

            <div className="space-y-3">
              {filteredCourses.map((course) => {
                const facultyNames =
                  course.faculty
                    ?.map((f) => {
                      const parts = f.displayName.split(", ");
                      return parts.length === 2 ? `${parts[1]} ${parts[0]}` : f.displayName;
                    })
                    .join(", ") || "TBA";
                const location =
                  course.meetingsFaculty?.[0]?.meetingTime?.buildingDescription
                    ? `${course.meetingsFaculty[0].meetingTime.buildingDescription} - ${course.meetingsFaculty[0].meetingTime.room}`
                    : "TBA";

                return (
                  <motion.div
                    key={course.courseReferenceNumber}
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                  >
                    <Card
                      onClick={() => toggleCourse(course)}
                      className={`cursor-pointer transition-all ${
                        selectedCourses.find((c) => c.courseReferenceNumber === course.courseReferenceNumber)
                          ? "border-primary shadow-lg"
                          : "hover:shadow-md"
                      }`}
                    >
                      <CardHeader className="p-4">
                        <CardTitle className="text-lg">{course.courseTitle}</CardTitle>
                      </CardHeader>
                      <CardContent className="p-4 pt-0">
                        <p className="text-sm text-muted-foreground">{course.courseMajor}</p>
                        <div className="mt-2 space-y-1">
                          <p className="text-xs text-muted-foreground">CRN: {course.courseReferenceNumber}</p>
                          <p className="text-xs text-muted-foreground">Faculty: {facultyNames}</p>
                          <p className="text-xs text-muted-foreground">Location: {location}</p>
                        </div>
                      </CardContent>
                    </Card>
                  </motion.div>
                );
              })}
            </div>
          </div>

          {/* Right Content (2/3 width) for FullCalendar schedule */}
          <div className="lg:col-span-2 bg-card rounded-xl shadow-sm border p-4">
            <FullCalendar
              plugins={[dayGridPlugin, timeGridPlugin]}
              initialView="timeGridWeek"
              events={calendarEvents}
              allDaySlot={false}
              slotMinTime="07:00:00"
              slotMaxTime="22:00:00"
              headerToolbar={{
                left: "",
                center: "",
                right: "",
              }}
              height="auto"
              expandRows={true}
              stickyHeaderDates={true}
              dayMaxEvents={true}
              eventTimeFormat={{
                hour: "2-digit",
                minute: "2-digit",
                meridiem: false,
              }}
              eventDidMount={(info) => {
                // Add tooltip with course details
                const event = info.event;
                const props = event.extendedProps;
                info.el.title = `${event.title}\nLocation: ${props.location}\nFaculty: ${props.faculty}`;
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}